@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
:root {
  --dark: #272727;
  --dark-rgb: 39, 39, 39;
  --siberian-blue: #1390e5;
  --gray: #1717194d;
  --red: #eb5757;
  --green: #00a241;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.divider {
  width: 100%;
  height: 1px;
  background: #c4c4c4;
}

.heading-2 {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 143%;
  color: black;
  margin: 0;
}

.heading-3 {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 156%;
  color: black;
  margin: 0;
}

.container {
  min-height: 100vh;
  height: 100%;
  background-color: #fafafa;
}

.content {
  width: 1040px;
  margin: 0 auto;
  padding: 40px 0;
}

.button {
  font-size: 20px;
  line-height: 120%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 14px 20px;
  border-radius: 5px;
  outline: none;
  border: 0;
  cursor: pointer;
}

.button_primary {
  background-color: var(--siberian-blue);
  color: white;
}

.button_secondary {
  padding: 13px 19px;
  background-color: transparent;
  color: var(--siberian-blue);
  border: 1px solid currentColor;
}

.button:disabled {
  opacity: 0.5;
  cursor: default;
}

.form-item {
  font-size: 16px;
  line-height: 22px;
}

.form-item {
  margin-bottom: 20px;
}

.form-item__label {
  display: block;
  color: var(--dark);
}

.form-item__label_required::before {
  content: "* ";
  color: var(--red);
}

.form-item__label_error {
  color: var(--red);
}

.form-item__error {
  color: var(--red);
  margin-top: 5px;
}

.input {
  font-size: 20px;
  line-height: 120%;
  border: 1px solid rgba(23, 23, 25, 0.2);
  color: var(--dark);
  padding: 11px 14px;
  border-radius: 5px;
  outline: none;
  transition: all ease-in-out 0.1s;
  width: 100%;
  background-color: transparent;
}

.input::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.3;
}

.input:-ms-input-placeholder {
  color: inherit;
  opacity: 0.3;
}

.input::placeholder {
  color: inherit;
  opacity: 0.3;
}

.input:hover {
  background: rgba(197, 228, 249, 0.1);
  border: 1px solid var(--siberian-blue);
}

.input:focus {
  background: rgba(197, 228, 249, 0.3);
  border: 1px solid var(--siberian-blue);
  opacity: 1;
}

.input:disabled {
  background: #fafafa;
  border: 1px solid rgba(23, 23, 25, 0.1) !important;
  color: rgba(var(--dark-rgb), 0.6);
}

.input_disabled::-webkit-input-placeholder, .input:disabled::-webkit-input-placeholder {
  color: #171719;
}

.input_disabled:-ms-input-placeholder, .input:disabled:-ms-input-placeholder {
  color: #171719;
}

.input_disabled::placeholder,
.input:disabled::placeholder {
  color: #171719;
}

.input_error {
  color: var(--red);
  background: rgba(235, 87, 87, 0.1) !important;
  border: 1px solid var(--red) !important;
}

.input_error::-webkit-input-placeholder {
  opacity: 0.6;
}

.input_error:-ms-input-placeholder {
  opacity: 0.6;
}

.input_error::placeholder {
  opacity: 0.6;
}

.password-input {
  position: relative;
}

.input + .password-input__eye {
  -webkit-filter: invert(100%) sepia(0%) saturate(1550%) hue-rotate(239deg)
    brightness(87%) contrast(86%);
          filter: invert(100%) sepia(0%) saturate(1550%) hue-rotate(239deg)
    brightness(87%) contrast(86%);
}

.input:focus + .password-input__eye {
  -webkit-filter: invert(45%) sepia(58%) saturate(2930%) hue-rotate(180deg)
    brightness(95%) contrast(88%);
          filter: invert(45%) sepia(58%) saturate(2930%) hue-rotate(180deg)
    brightness(95%) contrast(88%);
}

.password-input__eye {
  cursor: pointer;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
}

.alert {
  border: 1px solid currentColor;
  border-radius: 5px;
  padding: 14px 7px 13px;
  display: flex;
  grid-column-gap: 0.5rem;
  -webkit-column-gap: 0.5rem;
          column-gap: 0.5rem;
  align-items: center;
}

.alert_success {
  color: var(--green);
}

.alert_error {
  color: var(--red);
}

.alert__icon {
  width: 24px;
  height: 24px;
}

.alert__icon_error {
  -webkit-filter: invert(39%) sepia(38%) saturate(938%) hue-rotate(312deg)
    brightness(112%) contrast(99%);
          filter: invert(39%) sepia(38%) saturate(938%) hue-rotate(312deg)
    brightness(112%) contrast(99%);
}

.alert__content {
  font-size: 16px;
  line-height: 150%;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.login {
  width: 510px;
  padding: 40px 88px 60px;
  border: 1px solid rgba(39, 39, 39, 0.1);
  border-radius: 5px;
  background-color: white;
}

.login__title {
  text-align: center;
  margin-top: 32px;
  margin-bottom: 20px;
}

.login__logo {
  display: block;
  width: 88px;
  height: 88px;
  margin: auto;
}

.login-form .form-item__label {
  color: var(--gray);
}

.login-form__controls {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  margin-top: 40px;
}

.login-form__controls .button {
  width: 176px;
}

.login__error {
  margin-top: 20px;
}

.navbar-container {
  background: #ffffff;
  padding: 1rem;
  border-bottom: 1px solid #f1f1f1;
}

.navbar {
  margin: auto;
  width: 1040px;
}

.navbar-links {
  display: flex;
  justify-content: space-between;
}

.navbar-links__group {
  display: flex;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
  align-items: center;
}

.navbar__link {
  cursor: pointer;
  font-size: 18px;
  line-height: 156%;
  color: var(--siberian-blue);
  text-decoration: none;
  position: relative;
}

.navbar__link_active::after {
  content: "";
  display: block;
  width: 80%;
  height: 2px;
  position: absolute;
  top: calc(100% + 25px);
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--siberian-blue);
}

.schemas-list__item {
  background-color: white;
  position: relative;
  padding: 13px 20px;
  margin-bottom: 1px; /* for line */
}

.schemas-list__item a {
  color: #000000;
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
  text-decoration: none;
}

.schemas-list__item::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  background: #f1f1f1;
  left: 0;
  right: 0;
  bottom: -1px;
}

.schemas-page__schemas-list {
  margin: 40px 0;
}

.accordion-item {
  background: #fafafa;
  position: relative;
}

.accordion-item_opened {
  background: white;
}

.accordion-item:after {
  content: "";
  display: block;
  height: 1px;
  background-color: var(--gray);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.accordion-item__header {
  display: flex;
  justify-content: space-between;
  padding: 20px 36px;
  position: relative;
}

.accordion-item-title__arrow {
  position: absolute;
  top: 28px;
  left: 16px;
  transform: rotate(-90deg);
  transition: transform ease-in-out 0.1s;
}

.accordion-item-title__arrow_opened {
  transform: rotate(0deg);
}

.accordion-item-title {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 18px;
  line-height: 122%;
}

.accordion-item__extra {
  justify-self: flex-end;
}

.accordion-item__body {
  padding: 0 36px 40px 36px;
  display: none;
}

.accordion-item__body_opened {
  display: block;
}

.select {
  font-size: 20px;
  line-height: 120%;
  position: relative;
}

.select__header {
  display: flex;
  justify-content: space-between;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px;
  padding: 11px 15px;
  cursor: pointer;
  border: 1px solid rgba(23, 23, 25, 0.3);
  border-radius: 5px;
}

.select__header_opened {
  border-bottom: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-color: var(--siberian-blue);
}

.select__header_error {
  color: var(--red);
  border-color: currentColor;
  background: rgba(235, 87, 87, 0.1);
}

.select__placeholder {
  flex: 1 1;
  opacity: 0.3;
}

.select__placeholder_error {
  opacity: 0.6;
}

.select__value {
  flex: 1 1;
}

.select__body {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  border: 1px solid var(--siberian-blue);
  border-top: 0;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  background: white;
  z-index: 1;
}

.select__body_error {
  border-color: var(--red);
}

.select__body::after {
  content: "";
  height: 1px;
  background: rgba(23, 23, 25, 0.2);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.select__option {
  padding: 11px 14px;
  cursor: pointer;
  transition: all ease-in-out 0.1s;
}

.select__option:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.select__option:hover {
  background: rgba(197, 228, 249, 0.1);
}

.select__option_active {
  background: rgba(197, 228, 249, 0.3);
}

.options-list {
  margin-top: 22px;
  width: 367px;
  margin-left: auto;
}

.options-list__button {
  width: 100%;
}

.options-list-item {
  position: relative;
  margin-bottom: 20px;
}

.options-list-item__cross {
  position: absolute;
  top: 12px;
  right: 12px;
  -webkit-filter: invert(85%) sepia(1%) saturate(0%) hue-rotate(15deg) brightness(92%)
    contrast(95%);
          filter: invert(85%) sepia(1%) saturate(0%) hue-rotate(15deg) brightness(92%)
    contrast(95%);
}

.options-list-item .form-item:not(:last-child) {
  margin-bottom: 0.5rem;
}

.switch-container {
  display: flex;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}

.switch {
  width: 35px;
  height: 22px;
  border-radius: 19px;
  background: var(--gray);
  cursor: pointer;
  position: relative;
  border: 0;
  padding: 0;
  outline: none;
  transition: background ease-in-out 0.2s;
}

.switch_checked {
  background: var(--siberian-blue);
}

.switch_disabled {
  opacity: 0.2;
}

.switch_disabled-checked {
  opacity: 0.1;
}

.switch__indicator {
  position: absolute;
  background: #ffffff;
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  top: 3px;
  left: 3px;
  transition: all 0.1s ease-in;
}

.switch__indicator_checked {
  left: calc(100% - (1rem + 3px)); /* for transition */
}

.inline-label {
  font-size: 18px;
  line-height: 22px;
  color: var(--dark);
}

.inline-label_disabled {
  color: var(--gray);
  opacity: 0.6;
}

.string-validation-form__min-max {
  display: flex;
  grid-column-gap: 22px;
  -webkit-column-gap: 22px;
          column-gap: 22px;
}

.schema-field {
  width: 100%;
  display: flex;
  grid-column-gap: 108px;
  -webkit-column-gap: 108px;
          column-gap: 108px;
}

.schema-field__required {
  width: 386px;
}

.schema-field__validation {
  margin-top: 34px;
  width: 422px;
}

.fields-list-item__subtitle {
  color: var(--dark);
  opacity: 0.6;
  font-size: 14px;
  line-height: 157%;
  font-weight: normal;
  margin: 0;
}

.fields-list-item__remove {
  position: absolute;
  top: 13px;
  right: 20px;
}

.field-header {
  display: flex;
  grid-gap: 108px;
  gap: 108px;
}

.field-header__main {
  width: 386px;
}

.field-header__main_invalid {
  color: var(--red);
}

.schema-name {
  width: 422px;
  margin-bottom: 30px !important;
}

.schema-fields {
  margin-top: 40px;
}

.schema-fields__subtitle {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  line-height: 150%;
  color: black;
  opacity: 0.5;
  margin-top: 0.5rem;
}

.schema-fields__accordion {
  margin-top: 20px;
  margin-bottom: 42px;
}

.schema-fields__controls {
  display: flex;
  justify-content: space-between;
}

.schema-page__container {
  background-color: white;
  border-radius: 5px;
  padding: 36px;
}

.schema-page-form {
  width: 561px;
}

.schema-page-form__title {
  margin-bottom: 20px;
}

.schema-page-form__controls {
  margin-top: 20px;
  width: 100%;
}

.schema-page-form__alert {
  margin-top: 20px;
}

.checkbox-container {
  display: flex;
  align-items: center;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}

.checkbox {
  position: relative;
  color: var(--siberian-blue);
}

.checkbox_disabled {
  color: var(--gray);
  opacity: 0.3;
}

.checkbox__input {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
}

.checkbox__control {
  cursor: pointer;
  width: 16px;
  height: 16px;
  border-radius: 3px;
  border: 2px solid currentColor;
  opacity: 0.3;
}

.checkbox__checkmark {
  top: 50%;
  left: 50%;
  opacity: 0;
  position: absolute;
  transform: translate(-50%, -50%);
}

.checkbox__input:checked ~ .checkbox__checkmark {
  opacity: 1;
}

.checkbox__input:checked ~ .checkbox__control {
  border: 0;
  width: 20px;
  height: 20px;
  background-color: currentColor;
  opacity: 1;
}

